"use client"

import { useEffect, useState } from "react";
import Image from "next/image";

interface QRCodeDisplayProps {
  qrBase64Image?: string;
  qrString?: string;
  expiresAt?: string;
  onExpired?: () => void;
}

export function QRCodeDisplay({ qrBase64Image, qrString, expiresAt, onExpired }: QRCodeDisplayProps) {
  const [timeLeft, setTimeLeft] = useState<number | null>(null);
  
  // Ø­Ø³Ø§Ø¨ Ø§Ù„ÙˆÙ‚Øª Ø§Ù„Ù…ØªØ¨Ù‚ÙŠ Ù„Ø§Ù†ØªÙ‡Ø§Ø¡ ØµÙ„Ø§Ø­ÙŠØ© Ø±Ù…Ø² QR
  useEffect(() => {
    if (!expiresAt) return;
    
    const calculateTimeLeft = () => {
      const expiryTime = new Date(expiresAt).getTime();
      const now = new Date().getTime();
      const difference = expiryTime - now;
      
      if (difference <= 0) {
        setTimeLeft(0);
        if (onExpired) onExpired();
        return;
      }
      
      setTimeLeft(Math.floor(difference / 1000));
    };
    
    calculateTimeLeft();
    const timer = setInterval(calculateTimeLeft, 1000);
    
    return () => clearInterval(timer);
  }, [expiresAt, onExpired]);
  
  // Ø¥Ø°Ø§ Ù„Ù… ÙŠØªÙ… ØªÙˆÙÙŠØ± ØµÙˆØ±Ø© Ø±Ù…Ø² QR Ø£Ùˆ Ø³Ù„Ø³Ù„Ø© QR
  if (!qrBase64Image && !qrString) {
    return (
      <div className="flex flex-col items-center p-4 border rounded-md bg-gray-50">
        <p className="text-gray-500">Ø±Ù…Ø² QR ØºÙŠØ± Ù…ØªÙˆÙØ±</p>
      </div>
    );
  }
  
  return (
    <div className="flex flex-col items-center p-4 border rounded-md">
      {qrBase64Image ? (
        <div className="relative w-64 h-64">
          <Image
            src={qrBase64Image}
            alt="QR Code"
            fill
            className="object-contain"
            priority
          />
        </div>
      ) : (
        <div className="w-64 h-64 flex items-center justify-center bg-gray-100">
          <p className="text-sm text-gray-500">Ø±Ù…Ø² QR Ù…ØªÙˆÙØ± ÙƒÙ†Øµ ÙÙ‚Ø·</p>
        </div>
      )}
      
      <div className="mt-4 text-center">
        <p className="text-sm text-gray-700 font-medium">Ø§Ù…Ø³Ø­ Ø±Ù…Ø² QR Ø¨Ø§Ø³ØªØ®Ø¯Ø§Ù… ØªØ·Ø¨ÙŠÙ‚ WhatsApp Ø¹Ù„Ù‰ Ù‡Ø§ØªÙÙƒ</p>
        
        {timeLeft !== null && (
          <p className="text-xs text-gray-500 mt-1">
            {timeLeft > 0 
              ? `ÙŠÙ†ØªÙ‡ÙŠ ÙÙŠ ${Math.floor(timeLeft / 60)}:${(timeLeft % 60).toString().padStart(2, '0')}`
              : 'Ø§Ù†ØªÙ‡Øª ØµÙ„Ø§Ø­ÙŠØ© Ø§Ù„Ø±Ù…Ø²'}
          </p>
        )}
      </div>
      
      {qrString && (
        <div className="mt-2 w-full">
          <details className="text-xs">
            <summary className="cursor-pointer text-blue-600 hover:text-blue-800">Ø¹Ø±Ø¶ Ø±Ù…Ø² QR ÙƒÙ†Øµ</summary>
            <p className="mt-1 p-2 bg-gray-50 rounded-md break-all">{qrString}</p>
          </details>
        </div>
      )}
    </div>
  );
}
